
.site-nav .heard {
  position: relative;
  z-index: 1000;
}
.site-nav .heard_top {
  width: 100%;
  margin: 0 auto;
  height: 36px;
  font-size: 12px;
  background-color: #f6f6f6;
  min-width: 1080px;
}
.site-nav .heard_bop_inner {
  width: 1080px;
  height: 36px;
  margin: 0 auto;
  color: #666666;
}
.site-nav .heard_top_left {
  height: 36px;
  line-height: 36px;
  float: left;
  position: relative;
}
.site-nav .heard_top_icon {
  float: left;
}
.site-nav .heard_top_icon .fw {
  font-weight: 400;
}
.site-nav .heard_top_icon {
  float: left;
}
.site-nav .heard_top .qq_group {
  float: left;
  padding-left: 10px;
  position: relative;
}
.site-nav .heard_top .qq_group a {
  display: block;
  margin: 7px 0 0 0;
  height: 20px;
  line-height: 20px;
  padding: 0 5px 0 22px;
  border-radius: 2px;
  background: #f6f6f6;
  color: #666;
}
.site-nav .heard_top_appicon {
  padding: 0px 6px;
  height: 36px;
  line-height: 36px;
  position: relative;
  float: right;
  color: #484848;
  text-align: center;
  border-left: 1px solid #f8f8f8;
  border-right: 1px solid #f8f8f8;
  cursor: pointer;
  transition: all linear 0.2s;
  z-index: 1000;
}
.site-nav .heard_top_appicon img {
  vertical-align: middle;
  margin-top: -3px;
}
.site-nav .app_downewm {
  width: 111px;
  padding-top: 9px;
  padding-bottom: 10px;
  color: #999999;
  background-color: #ffffff;
  position: absolute;
  left: -14px;
  top: 36px;
  border-left: 1px solid #eeeeee;
  border-right: 1px solid #eeeeee;
  border-bottom: 1px solid #eeeeee;
  display: none;
  border-radius: 8px;
  z-index: 9999;
  box-shadow: 0px 4px 4px #e0e0e0;
}
.site-nav .app_downewm p {
  line-height: 20px;
}
.site-nav .app_downewm p.second {
  color: #ff5358;
}
.site-nav .clear {
  clear: both;
}
.site-nav .heard_top_right {
  height: 36px;
  float: right;
}
.site-nav .heard_top_li {
  float: right;
  padding: 0 15px;
  height: 36px;
  line-height: 36px;
  position: relative;
}
.site-nav .heard_top_li .changeLink {
  color: #fff;
  background: #ff5256;
  display: block;
  padding: 0 10px;
  height: 36px;
  line-height: 36px;
}
.site-nav #heard_top_li_huodong {
  height: 12px;
  margin: 12px 0 0 15px;
  line-height: 12px;
}
.site-nav .heard_top_lia {
  float: right;
  height: 36px;
  line-height: 36px;
  padding-left: 15px;
}
.site-nav #heard_top_li_login {
  height: 12px;
  margin: 13px 0 0 15px;
  line-height: 12px;
}
.site-nav .heard_bottom {
  width: 100%;
  margin: 0 auto;
  height: 89px;
  background-color: #fff;
  min-width: 1080px;
  border-bottom: 1px solid #e5e5e5;
}
.site-nav .heard_bottom_inner {
  width: 1080px;
  margin: 0 auto;
  height: 90px;
}
.site-nav .heard_logo {
  float: left;
  width: 380px;
  padding-top: 9px;
  height: 81px;
  position: relative;
}
.site-nav .heard_logo .logoTip_cg {
  position: absolute;
  right: 0px;
  top: 5px;
}
.site-nav .heard_nav {
  float: right;
  width: 700px;
  color: #484848;
  height: 90px;
}
.site-nav .heard_nav ul {
  margin-top: 26px;
}
.site-nav .heard_nav ul li {
  float: right;
  padding: 0 15px;
  position: relative;
  height: 40px;
  line-height: 40px;
}
.site-nav .heard_nav ul li a {
  font-size: 16px;
}
.site-nav .heard_nav ul .myinvest {
  background-color: #FFF;
}
.site-nav .heard_nav ul .myinvest .invest {
  display: block;
  width: 100px;
  text-align: center;
  position: relative;
  z-index: 5555;
  border: 1px solid #fff;
  padding-right: 17px;
}
.site-nav .myinvest_img {
  margin-left: 9px;
  position: absolute;
  top: 18px;
  right: 18px;
}
.site-nav .heard_nav ul li .head_ximulist {
  position: absolute;
  left: 0;
  top: 40px;
  font-size: 14px;
  color: #666;
  width: 117px;
  z-index: 100;
  background-color: #FFF;
  border: 1px solid #eee;
  border-top: 0;
  box-shadow: 0 0 1px #eaeaea;
}
.site-nav .heard_nav ul li .head_ximulist a {
  border-bottom: 1px solid #eee;
  text-align: left;
}
.site-nav .head_ximulist a b {
  font-family: "microsoft yahei";
  font-size: 14px;
  font-weight: 400;
  margin-left: 17px;
}
.site-nav .head_ximulist a label {
  font-family: "microsoft yahei";
  color: #999;
  font-size: 14px;
  margin-right: 3px;
}
.site-nav .heard_nav ul li .head_ximulist {
  position: absolute;
  left: 0;
  top: 40px;
  font-size: 14px;
  color: #666;
  width: 117px;
  z-index: 100;
  background-color: #FFF;
  border: 1px solid #eee;
  border-top: 0;
  box-shadow: 0 0 1px #eaeaea;
}
.site-nav .heard_nav ul li .head_ximulist a {
  border-bottom: 1px solid #eee;
  text-align: left;
}
.site-nav .head_ximulist a span {
  color: #e25353;
}



